global.jQuery = global.$ = window.jQuery = require('jquery');
var skrollr = require('skrollr');
require('jquery.easing');
require('jquery.inview');
require('swiper');
require('jquery.matchHeight');

$(function () {
	
/*---------------------------------------------
	Popup Window
---------------------------------------------*/

$.fn.openwin = function (settings) {
	settings = $.extend({
		option: {
			width: false,
			height: false,
			top: false,
			left: false,
			menubar: 'no',
			toolbar: 'no',
			location: 'no',
			status: 'no',
			resizable: 'no',
			scrollbars: 'no',
			directories: 'no',
			titlebar: 'yes',
			fullscreen: 'no'
		},
		target: '_blank',
		blur: false,
		focus: false
	}, settings);

	$(this).on('click', function () {
		var options = [];
		var target = settings.target;
		var url = $(this).attr('href');

		if ($(this).attr('rel')) {
			var rel = $(this).attr('rel').split(',');
			var relOption = {};
			$.each(settings.option, function (i, rel) {
				var a = rel[i].split('=');
				relOption[a[0]] = a[1];
			});
		}

		$.each(settings.option, function (k) {
			var v = settings.option[k];
			if (relOption) {
				if (relOption[k]) {
					v = relOption[k];
				}
			}
			if (v) {
				options.push(k + '=' + v);
			}
		});
		if (relOption) {
			if (relOption['target']) {
				target = relOption['target'];
			}
		}
		var newWin = window.open(url, target, options.join(','));
		if (settings.blur === true) {
			newWin.blur();
		}
		if (settings.focus === true) {
			newWin.focus();
		}
		return false;
	});
	return this;
	};

	$('a.openwin').openwin({
		option: {
			width: 1200,
			height: 900,
			top: false,
			left: false,
			menubar: "no",
			toolbar: "no",
			location: "no",
			status: "no",
			resizable: "yes",
			scrollbars: "yes",
			directories: "no",
			titlebar: "yes",
			fullscreen: "no"
		}
	});


/*---------------------------------------------
	humberger Menu
---------------------------------------------*/


	var flag = false,
		$navGlobal = $('.l-navGlobal'),
		$header = $('header'),
		$humberger = $('.humberger'),
		windowWidth = $(window).width(),
		windowHeight = $(window).innerHeight(),
		windowWidth = $(window).width(),
		xsNav = $('.xs_nav').innerHeight(),
		xsHeight = windowHeight - xsNav;

	$humberger.on('click', function () {
		if (!flag) {
			$humberger.addClass('active');
			$navGlobal.fadeIn(function () {
				flag = true;
			});
		} else {
			$humberger.removeClass('active');
			$navGlobal.fadeOut(function () {
				flag = false;
			});
		}
	});

	$.fn.animate2 = function (properties, duration, ease) {
		ease = ease || 'ease';
		var $this = this;
		var cssOrig = {
			transition: $this.css('transition')
		};
		return $this.queue(next => {
			properties['transition'] = 'all ' + duration + 'ms ' + ease;
			$this.css(properties);
			setTimeout(function () {
				$this.css(cssOrig);
				next();
			}, duration);
		});
	};


/*--------------------------------------------------------------------------*
* スワイプコンテナ設定
*--------------------------------------------------------------------------*/


	//var swiper = new Swiper('.swiper-container', {
	//	initialSlide: 1,
	//	pagination: '.swiper-pagination',
	//	paginationClickable: true,
	//	speed: 400,
	//	autoplay:5000,
	//	nextButton: '.swiper-button-next',
	//	prevButton: '.swiper-button-prev',
	//	spaceBetween: 0
	//});


/*--------------------------------------------------------------------------*
 * inview
 *--------------------------------------------------------------------------*/


//汎用設定

	$('.js-inview').css('opacity', '0').css('top', '50px');

	$('.js-inview').on('inview', function () {
		$(this).stop().animate({
			opacity: '1',
			top: '0'
		}, 1500);
	});

	$(function () {
		$('.js-iframe').on('load', function () {
				try {
					$(this).height(this.contentWindow.document.documentElement.scrollHeight);
				} catch (e) {}
			})
			.trigger('load');
	});
	
	
	
	
});



/*--------------------------------------------------------------------------*
 * スクローラー設定
 *--------------------------------------------------------------------------*/

var isMobile = false;

isMobile = $('html').is('.skrollr-mb');

if (!isMobile) {
	window.onload = function () {
		var s = skrollr.init({
			forceHeight: false,
			mobileDeceleration: 0.01,
			mobileCheck: function () {
				return isMobile;
			}
		});
	}
}